import React from 'react';
import message from 'shared/lib/message';

const Test: React.FC = () => {
  const onClick = () => {
    message.info('hello world 1');
    setTimeout(() => {
      message.info('hello world 2', undefined, () => {
        console.log('close 2');
      });
    }, 500);
    setTimeout(() => {
      message.info('hello world 3', undefined, () => {
        console.log('close 3');
      });
    }, 700);
  };

  return (
    <>
      <button onClick={onClick}>click me</button>
    </>
  );
};

export default Test;
